import React from 'react';
import logo from './icon.png';
import './App.css';
import Button from "react-bootstrap/Button";
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Container from 'react-bootstrap/Container';



const Header2 = () => (
  <div class="card" >
    <div class="card-body headerStyle">
      Education Background
    </div>
  </div>);
const Header3 = () => (
  <div class="card" >
    <div class="card-body headerStyle">
      Education History
      
    </div>
  </div>);

const Header4 = () => (
  <div class="card" >
    <div class="card-body headerStyle">
      Skills
      
    </div>
  </div>);

const Education = () => (
  <div class="card">
    <div class="card-body education1">
     Full Stact Developer<br/>
     Photographer <br/>
     Graphic Design <br/>
     NSC 2015  <br/>
     Computer Literacy  <br/>
     Video Editor/Videographer <br/>
     Sound Engineer <br/>
     A+ and IT <br/>
     Water meter Devices ( Technician Training )<br/>
     
    </div>
  </div>);

const Skills = () => (
  <div class="card">
    <div class="card-body education1">
     <button>Leadership</button><br/> <br/>
     <button>Responsible</button><br/><br/>
     <button>Reliable</button><br/><br/>
     

     <button>Good communication</button>

    </div>
  </div>);
function Edu() {
  return (
    <div className="App">
      <body className="bodyStructure">
          <div class="container">
            <div class="row">
              <div class="col-sm">
                <Header2 />
              </div>
            </div><br/>
            <div class="row">
              <div class="col-sm">
                <Header3 />
              </div>
              <div class="col-sm">
                <Header4 />
              </div>
            </div><br/>
            <div class="row">
              <div class="col-sm">
                <Education />
              </div>
              <div class="col-sm">
                <Skills/>
              </div>
            </div>               
          </div>
      </body>
    </div>
  );
}

export default Edu;
